@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}body{font-family:"Montserrat",sans-serif;scroll-behavior:smooth}a,button{cursor:pointer}
.header{display:flex;justify-content:space-between;min-height:45px;position:fixed;top:0;width:100%;background-color:#000;padding-top:30px}.navbar a{color:#fff;margin-right:100px;text-decoration:none;font-size:13px;letter-spacing:3px;padding:30px 20px 28px 20px;position:relative}.navbar a::before{content:"";position:absolute;bottom:18px;right:0;width:0;height:2px;background-color:#fff;transition:width .6s cubic-bezier(0.25, 1, 0.5, 1)}.navbar a:hover::before{left:10px;right:10px;width:80%;text-align:center}.logo{font-size:15px;padding-left:50px;color:#fff;letter-spacing:.7rem}.burger-btn{outline:none;border:none;font-size:17px;color:#fff;font-weight:700;background:none;cursor:pointer;display:none}@media(max-width: 1100px){.burger-btn{display:block !important;position:absolute;right:50px;top:30px}.header{display:flex;flex-direction:column;align-items:center}.navbar{display:flex;flex-direction:column;visibility:hidden;height:0}.logo{padding-left:0px}.showHeight{height:auto;visibility:visible}}@media(max-width: 600px){.header{align-items:flex-start}.logo{width:50%;padding:2px 0px 20px 30px;line-height:22px}}
.main-section{background-image:url(/static/media/background-img.585d0033.jpg);background-repeat:no-repeat;background-size:cover;background-position:center center;height:100vh;width:100%;color:#f5f5f5}.main-section h1{padding-top:330px;padding-left:170px;font-size:80px;font-weight:500;margin-bottom:10px}.main-section p{padding:10px 173px;font-size:23px;letter-spacing:.5rem}.main-section button{margin-right:20px;margin-top:20px;background:none;outline:none;width:220px;height:45px;border:1px solid #fff;border-radius:30px;text-transform:uppercase;transition:-webkit-transform .5s;transition:transform .5s;transition:transform .5s, -webkit-transform .5s;color:#fff;text-decoration:none}.main-section button:hover{background-color:#222;border:1px solid #f5f5f5;-webkit-transform:scale(1.1);transform:scale(1.1)}.main-section .github-btn{margin-left:170px}@media(max-width: 1100px){.main-section h1{padding-left:50px;font-size:60px}.main-section p{padding-left:50px;font-size:25px}.github-btn{margin-left:50px !important}h1{padding-top:230px !important}}@media(max-width: 600px){.main-section h1{font-size:40px}.main-section p{font-size:20px;line-height:23px}.github-btn{margin-right:80px !important}.main-section button{margin-left:50px !important}h1{padding-top:190px !important}}@media(max-width: 350px){.main-section button{margin-right:0px !important}}
.projects-section{margin:50px 50px 110px}.projects-section h2{text-align:center;font-size:30px;font-weight:700;letter-spacing:7px;margin-bottom:60px}.project{display:flex;width:80%;margin:auto;box-shadow:rgba(149,157,165,.2) 0px 8px 24px;padding:40px}.project img{max-width:100%;height:auto;image-resolution:from-image 300dpi}.project .project-img{width:100%;margin:20px}.project .project-p{margin:20px;width:100%}.project .project-p p{line-height:20px;margin-bottom:20px}.project .project-p h4{margin-bottom:30px;font-weight:700}.project .project-p button{margin-right:20px;width:110px;padding:10px;border-radius:25px;outline:none;background-color:#000;color:#fff;border:none;cursor:pointer;font-weight:700;letter-spacing:1px;transition:-webkit-transform .5s;transition:transform .5s;transition:transform .5s, -webkit-transform .5s}.project .project-p button:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.project .project-p button a{text-decoration:none;color:#fff}.project .project-p .source-btn{background:none;color:#000;border:1px solid #000}.project .project-p .source-btn a{color:#000}h3{text-align:center;font-size:23px;font-weight:700;letter-spacing:10px;margin:70px auto;padding-bottom:10px;border-bottom-left-radius:20px;border-top-right-radius:20px;text-decoration:underline;-webkit-text-decoration-color:#0f0f0f;text-decoration-color:#0f0f0f}@media(max-width: 1200px){.project{flex-wrap:wrap}}@media(max-width: 850px){.project{padding:10px}.projects-section h3{font-size:20px;width:300px;margin-bottom:30px}}@media(max-width: 475px){.source-btn{margin-top:20px}}@media(max-width: 400px){.projects-section h3{font-size:14px;width:200px}}
.footer{background-color:#f1f1f1;padding-top:120px;padding-bottom:150px}.footer .footer-row{display:flex;justify-content:center;width:100%;background-color:#fff;padding-top:130px;padding-bottom:90px}.footer-links{color:#000;width:40%;padding-top:30px}.footer-links a{color:#000;margin-right:40px;font-size:25px;text-decoration:none;transition:.3s linear}.footer-links a:hover{color:#f1f1f1}.footer-links .footer-text{font-size:12px;letter-spacing:2px;margin-top:15px}.footer-input{width:35%}.footer-input h4{font-size:45px;padding-bottom:20px;font-weight:700}.footer-input .footer-p{color:#000;font-size:17px;margin-bottom:20px}.footer-input .newsletter-form{display:flex;align-items:center}.footer-input .newsletter-form input{padding:18px;font-size:16px;color:#01040e;background:#ddd;border:none;font-weight:700;outline:none;border-radius:30px;width:250px;text-align:center}.footer-input .newsletter-form button{padding:18px 40px;font-size:16px;color:#fff;background:#000;border:none;font-weight:700;outline:none;border-radius:30px;margin-left:20px;cursor:pointer;transition:opacity .3s linear}.footer-input .newsletter-form button:hover{opacity:.7}.footer-button button{position:fixed;right:65px;bottom:40px;background-color:#000;color:#fff;outline:none;border:none;padding:10px;font-size:25px;width:50px;border-radius:2px}.footer-button button:hover{opacity:.7}@media(max-width: 1200px){.footer-row{flex-wrap:wrap;padding-left:0;justify-content:flex-end}.footer-row .footer-links{width:60%;margin-bottom:50px;padding-top:0px}.footer-row .footer-input{width:60%}}@media(max-width: 700px){.footer-row{padding-top:60px !important}.newsletter-form{flex-wrap:wrap;width:90%}.newsletter-form input{margin-bottom:20px;width:80% !important}.newsletter-form button{width:90%;margin-left:0px !important}.footer-button button{right:30px;font-size:20px;width:45px}}@media(max-width: 500px){.footer-input h4{font-size:35px}.footer-button button{right:10px}}
.aboutme-container{padding:90px;background-color:#f1f1f1;text-align:center}.aboutme-container h2{text-align:center;font-size:30px;font-weight:700;letter-spacing:7px;margin-bottom:40px}.container-row{display:flex;justify-content:center;margin:auto;width:90%}.container-row .cv-col{width:80%;margin:20px}.container-row .cv-col p{line-height:30px;font-size:18px;padding:20px;transition:.75s ease-in-out}.container-row .cv-col button{background-color:#000;outline:none;margin:20px;height:42px;width:170px;border-radius:20px;border:none;transition:-webkit-transform .5s;transition:transform .5s;transition:transform .5s, -webkit-transform .5s}.container-row .cv-col button:hover{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#363434}.container-row .cv-col button a{text-decoration:none;color:#fff;font-weight:600}.container-row .skills-col{width:40%;margin:20px}.container-row .skills-col h5{margin:20px;font-size:23px;font-weight:600}.container-row .skills-col .skills-p{font-weight:300;margin:20px;padding:20px}@media(max-width: 900px){.aboutme-container{padding:30px}}@media(max-width: 550px){.cv-col{width:100%}.cv-col p{font-size:15px !important;padding:10px !important}}
